import React from "react";
import Link from "gatsby-link";
import './index.scss'
import {kebabCase, sample } from 'lodash';

const buttonClasses = ['-green', '-blue', '-salmon', '-sun', '-alge', '-flower']

export const TagButton = ({text, link}) => {
    const tagLink = `/tags/${kebabCase(link)}/`;
    const buttonClass = `button ${sample(buttonClasses)}`;
    return (
        <Link to={tagLink} className={buttonClass}>
            {text}
        </Link>
    )
}



